"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Loader2, Trash2, Edit2, Search } from "lucide-react"
import { getReadingData, saveReadingDay, searchGoogleBooks } from "@/lib/api-client"

export function MonthCalendar({ month, days, year, userEmail, monthIndex }: any) {
  const [readings, setReadings] = useState<any[]>([])
  const [tempBookNames, setTempBookNames] = useState<Record<number, string>>({})
  const [suggestions, setSuggestions] = useState<any[]>([])
  const [activeDay, setActiveDay] = useState<number | null>(null)
  const [isUpdating, setIsUpdating] = useState(false)

  const now = new Date(); now.setHours(0,0,0,0)

  async function loadData() {
    if (!userEmail) return
    try {
      const response: any = await getReadingData(userEmail, year)
      setReadings(Array.isArray(response?.data) ? response.data : (Array.isArray(response) ? response : []))
    } catch (err) { console.error(err) }
  }

  useEffect(() => { loadData() }, [userEmail, monthIndex, year])

  const handleSearch = async (day: number, val: string) => {
    setTempBookNames(p => ({ ...p, [day]: val }))
    if (val.length >= 3) {
      setActiveDay(day)
      const books = await searchGoogleBooks(val)
      setSuggestions(books)
    } else { setSuggestions([]) }
  }

  async function handleAction(day: number, action: any, bookName?: string, coverUrl?: string, totalPages?: number) {
    if (isUpdating || !userEmail) return
    const name = bookName || tempBookNames[day]
    if (!name) return
    setIsUpdating(true)
    
    const date = new Date(year, monthIndex, day, 12, 0, 0).toISOString()
    
    try {
      await saveReadingDay(userEmail, year, monthIndex + 1, day, date, date, name, action, coverUrl, totalPages)
      setTempBookNames(p => ({ ...p, [day]: "" }))
      setSuggestions([]); setActiveDay(null)
      await loadData() 
    } catch (err) { alert("Erro ao salvar.") } finally { setIsUpdating(false) }
  }

  return (
    <div className="grid grid-cols-2 gap-3 md:grid-cols-7 md:gap-4">
      {Array.from({ length: days || 31 }, (_, i) => i + 1).map((day) => {
        const currentLoopDate = new Date(year, monthIndex, day); currentLoopDate.setHours(0,0,0,0)
        const isFuture = currentLoopDate > now
        const isToday = currentLoopDate.getTime() === now.getTime()

        const dayReadings = readings.filter((r) => {
          const start = new Date(r.start_date); start.setHours(0,0,0,0)
          const end = r.end_date ? new Date(r.end_date) : null
          if (end) end.setHours(0,0,0,0)
          return currentLoopDate >= start && (!end || currentLoopDate <= end)
        })

        return (
          <div key={day} className={`min-h-[160px] rounded-xl border p-2 flex flex-col bg-white shadow-sm ${isToday ? 'ring-2 ring-primary' : ''} ${isFuture ? 'opacity-30 pointer-events-none' : ''}`}>
            <span className="font-bold text-xs mb-2 opacity-50">{day}</span>
            
            <div className="flex-1 space-y-2 overflow-y-auto scrollbar-hide">
              {dayReadings.map((r, idx) => (
                <div key={idx} className="flex gap-2 p-1.5 bg-slate-50 rounded-lg border text-[10px] items-center">
                  {r.cover_url && <img src={r.cover_url} className="w-6 h-9 object-cover rounded shadow-xs" />}
                  <p className="font-bold truncate flex-1">{r.book_name}</p>
                  {r.status === 'lendo' && <button onClick={() => handleAction(day, "FINISH_READING", r.book_name)} className="text-primary font-bold">✓</button>}
                </div>
              ))}
            </div>

            {!isFuture && (
              <div className="mt-2 pt-2 border-t relative">
                <Input 
                  className="h-7 text-[16px] md:text-[10px]" 
                  placeholder="Livro..." 
                  value={tempBookNames[day] || ""} 
                  onChange={e => handleSearch(day, e.target.value)} 
                />
                
                {activeDay === day && suggestions.length > 0 && (
                  <div className="absolute z-50 bottom-full left-0 w-full bg-white border rounded-lg shadow-xl mb-1 max-h-40 overflow-y-auto">
                    {suggestions.map((s, i) => (
                      <div key={i} className="p-2 hover:bg-slate-50 cursor-pointer flex gap-2 border-b text-[9px]" onClick={() => handleAction(day, "START_READING", s.title, s.thumbnail, s.pageCount)}>
                        <img src={s.thumbnail} className="w-5 h-7 object-cover" />
                        <span className="truncate">{s.title}</span>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            )}
          </div>
        )
      })}
    </div>
  )
}